.home{
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
}

.home__image{
    width: 100%;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0,1), rgba(0, 0, 0, 0));
    z-index: -1;
    margin-bottom: -150px;
}

.home__row{
    display: flex;
    z-index: 1;
    margin: 0px 5px 0px 5px;
}