.checkoutProduct{
    display: flex;  
    margin: 20px 0px 20px 0px;
}
.checkoutProduct__image{
    object-fit:contain;
    width:180px;
    height: 180px;
}
.checkoutProduct__info{
    padding-left:20px
}
.checkoutProduct__info > button{
    background: #f0c14b;
    border: 1px solid ;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111
}
.checkoutProduct__title{
    font-size:17px;
    font-weight:800;
}
